<script>
import axios from 'axios'

export default {

  data() {

    return {
      file: [],
      url: null,
      isloaded: false,
      form: {},
      result: {},
      info: null,
      info_result: null,
      value: "",
      errors: [],
      service_name: null,
      administrator: null,
      phone: null,
      whatsApp: null,
      address: null,
      facebook:null,
       info_cat: null,
        info_dep: null,
        items:[],
    selectedOption:'',
    cat_id:"",
    categ_id:null,
    post_dep:""
    }
  },
  mounted() {
    axios
      .post("https://wasselni.ps/registration/preparation_registration")
      .then((response) => {
        this.isloaded = true
        this.info = response.data.result.all_countries
        this.info_cat = response.data.result.all_categories
        this.info_dep = response.data.result.all_categories.all_department
        //console.log(this.isloaded)
      })
  },
  methods: {
    onFileChange(e) {
      const file = e.target.files[0]
      this.url = URL.createObjectURL(file)
    },
    onChange:function() {
      this.items = [];
      let main_id ={categ_id:this.cat_id};


      axios.post('https://wasselni.ps/registration/get_list_departments',main_id)
   .then((response) => {
        this.info_dep = response.data.result.all_departments
        console.log(this.info_dep)
      })
    },
    submitForm: async function (event) {
      event.preventDefault()
       this.errors = []
      if (this.service_name && this.phone) {
       this.errors = []
       var formHTML = event.target
      var formData = new FormData(formHTML)
      axios
        .post("https://wasselni.ps/registration/add_info", formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        })
        .then((response2) => {
          this.info_result = response2.data.message;
          setTimeout(() => {
            this.$toast.success('تم الأشتراك بنجاح', {
              position:'bottom-left',
            })
            this.service_name=null,
            this.administrator=null,
            this.phone=null,
            this.whatsApp=null,
            this.address=null,
            this.facebook=null,
            setTimeout(window.location.href = 'https://wasselni.ps/index.html',500)
          },500)
        });
      }
else {
      if (!this.service_name_en || this.service_name_en == null) {
        this.errors.push('عنوان مقدم الخدمة مطلوب')
      }

       if (!this.phone || this.phone == null) {
        this.errors.push('رقم الجوال مطلوب')
      }
}

        }
  }
}
</script>
