import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
// import About from '../views/About.vue'
// import Registeration from '../views/Registration.vue'

const routes = [{
        path: '/',
        name: 'Home',
        component: Home,
        meta: { auth: true, title: 'اشترك معانا-وصلنى', keywords: 'مقدم خدمة وصلنى' }
    }
    // {
    //   path: '/about',
    //   name: 'About',
    //   component: About,
    //   meta: { auth: true, title: 'vvcvcvcxv', keywords: 'dddd' }

    // },
    // {
    //   path: '/registeration',
    //   name: 'Registeration',
    //   component: Registeration,
    //   meta: { auth: true, title: 'Buckets', keywords: 'dddd' }
    // }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})
router.beforeEach((to, from, next) => {
    document.title = to.meta.title
    document.keywords = to.meta.keywords
    next()
})

export default router